// ==========================================================================
// Modifiers
// ==========================================================================

.icon--xs {
    font-size: 0.625rem;
}

.icon--sm {
    font-size: 1.125rem;
}

.icon--md {
    font-size: 1.5rem;
}

.icon--lg {
    font-size: 2.125rem;
}

.icon--xl {
    font-size: 2.75rem;
}

.icon--circle {
    color: white;
    padding: .5em;
    border-radius: 50%;
    display: inline-block;
}

.icon--outline {
    padding: .3em;
    border: 2px solid;
    border-radius: 50%;
    font-weight: 600;
}

.icon-do-not {
	position: relative;
	color: $status-3;
	z-index: 5;
	font-size: 1.8rem !important;

	&:before {
		content: "\e99c";
	}

	> [class^="icon-"], [class*=" icon-"] {
		float: left;
		position: absolute;
		left: 0;
		top: 0;
		margin: 0.3em;
		color: #37424a;
		z-index: -1;
		font-size: 1.125rem;
	}

	&.icon--xs {
		font-size: 1rem !important;

		> [class^="icon-"], [class*=" icon-"] {
      font-size: 0.625rem;
		}
	}

	&.icon--sm {
		font-size: 1.8rem !important;

		> [class^="icon-"], [class*=" icon-"] {
      font-size: 1.125rem;
		}
	}

	&.icon--md {
		font-size: 2.4rem !important;

		> [class^="icon-"], [class*=" icon-"] {
      font-size: 1.5rem;
		}
	}

	&.icon--lg {
		font-size: 3.4rem !important;

		> [class^="icon-"], [class*=" icon-"] {
      font-size: 2.125rem;
		}
	}

	&.icon--xl {
		font-size: 4.4rem !important;

		> [class^="icon-"], [class*=" icon-"] {
      font-size: 2.75rem;
		}
	}
}
